.navbar-container {
  color: var(--black);
  font-family: "Fira Code", sans-serif;
  max-width: 1200px;
  position: relative;
}
.navbar-container .responsive-logo-container {
  display: none;
}
.navbar-container .hamburger-open-close {
  position: absolute;
  top: 30px;
  right: 30px;
  font-size: 24px;
  display: none;
  z-index: 4000;
}
.navbar-container .navbar-nav {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
  margin: 0 auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3000;
  transition: all 500ms ease;
  background-color: var(--light);
}
.navbar-container .navbar-nav.scroll {
  box-shadow: 5px 10px 15px rgba(0, 0, 0, 0.2);
  background-color: var(--white);
}
.navbar-container .navbar-nav-logo-container img {
  width: 80px;
  height: auto;
}
.navbar-container .navbar-nav-list {
  display: flex;
  justify-content: space-between;
  gap: 25px;
  align-items: center;
}
.navbar-container .navbar-nav-list-item.active {
  font-weight: bold;
}
.navbar-container .navbar-nav-list-item-link {
  text-decoration: none;
  font-size: 13px;
  color: var(--black);
}
.navbar-container .navbar-nav-search-button {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar-container .navbar-nav-search-button-image {
  width: 25px;
  height: auto;
}
.navbar-container .navbar-nav-login-button {
  transition: all 350ms ease;
}
.navbar-container .navbar-nav-login-button a {
  padding: 10px 35px;
  background-color: var(--black);
  color: var(--white);
  font-weight: bold;
  font-size: 14px;
  text-decoration: none;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  cursor: pointer;
}
.navbar-container .navbar-nav-login-button:hover {
  transform: translateY(-3px);
}

@media screen and (max-width: 850px) {
  .navbar-container {
    overflow: hidden !important;
  }
  .navbar-container .responsive-logo-container {
    display: block;
  }
  .navbar-container .responsive-logo-container img {
    width: 80px;
    height: auto;
    margin-left: 30px;
  }
  .navbar-container .hamburger-open-close {
    display: block;
    cursor: pointer;
    z-index: 5000;
  }
  .navbar-container .navbar-nav {
    gap: 30px;
    display: none;
    height: 100%;
    max-width: 100%;
  }
  .navbar-container .navbar-nav.active {
    display: flex;
    flex-direction: column;
  }
  .navbar-container .navbar-nav-list {
    flex-direction: column;
  }
  .navbar-container .navbar-nav-logo-container img {
    display: none;
  }
}
