.main-section {
  display: flex;
  width: 100%;
  height: 100vh;
  position: relative;
  top: 20px;
}
.main-section .navigation-buttons .main-section-prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  color: white;
  z-index: 500;
  background-color: black;
  padding: 10px;
  cursor: pointer;
}
.main-section .navigation-buttons .main-section-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
