}
.main-section .slide-2 .main-image img {
  transform: rotate(45deg);
}
.main-section .slider {
  position: absolute;
  opacity: 0;
  transition: all 350ms ease;
  height: 100%;
  width: 1000px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  left: 50%;
  transform: translateX(-50%);
  gap: 10px;
}
.main-section .slider.active {
  opacity: 1;
}
.main-section .slider .main-image img {
  height: 250px;
  width: auto;
  background: none;
}
.main-section .slider .content {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.main-section .slider .content h1 {
  font-size: 48px;
  font-weight: bold;
  line-height: 4rem;
  font-family: "DM Serif Display", sans-serif;
}
.main-section .slider .content p {
  font-size: 16px;
  line-height: 1.8rem;
  font-weight: normal;
  font-family: "Fira Code", sans-serif;
  opacity: 0.7;
}
.main-section .slider .content .button-container {
  z-index: 2000;
  margin-top: 20px;
  height: 70px;
  cursor: pointer;
  transition: all 350ms ease;
}
.main-section .slider .content .button-container:hover {
  transform: translateY(-3px);
}
.main-section .slider .content .button-container .main-section-button {
  padding: 12.5px 50px;
  background-color: var(--black);
  color: var(--white);
  font-weight: bold;
